.popup__input {
  width: 358px;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  margin: 30px auto 0;
  box-sizing: border-box;
  padding: 0 0 9px;
  border: 0;
  border-bottom: 1px solid rgba(84, 84, 84, 0.25);
  font-family: inherit;
  margin-top: 0;
}

/* .popup__input:first-of-type {
   margin-top: 48px;
}
 */
.popup__input:focus {
  outline: none;
}

@media (max-width: 600px) {
  .popup__input {
    width: 238px;
  }
